.Link {
  padding: 0;
  line-height: inherit;
  color: var(--accent-primary);
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  transition: none;
}
.Link:not(:disabled):hover {
  color: var(--accent-primary-hover);
}
.Link:not(:disabled):active {
  color: var(--accent-primary-active);
}
.Link:disabled {
  cursor: not-allowed;
  opacity: var(--opacity-disabled);
}
.Link > .LemonIcon:last-child {
  margin-left: 0.15em;
}
.Link--subtle {
  color: var(--text-3000);
}
.Link--subtle:not(:disabled):hover {
  color: var(--accent-primary-hover);
}
