.profile-package {
  display: inline-flex;
  align-items: center;
}
.profile-package:not(:first-child) {
  margin-left: 0.375rem;
}

.profile-name {
  margin-left: 0.375rem;
  font-weight: 500;
}

.ProfilePicture {
  position: relative;
  display: inline-flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  user-select: none;
  border-radius: 50%;
}
.ProfilePicture.xxl {
  width: 48px;
  height: 48px;
  font-size: 20px;
  line-height: 48px;
}
.ProfilePicture.xl {
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
}
.ProfilePicture.lg {
  width: 32px;
  height: 32px;
  font-size: 18px;
  line-height: 32px;
}
.ProfilePicture.md {
  width: 24px;
  height: 24px;
  font-size: 14px;
  line-height: 24px;
}
.ProfilePicture.sm {
  width: 18px;
  height: 18px;
  font-size: 12px;
  line-height: 18px;
}
.ProfilePicture.xs {
  width: 16px;
  height: 16px;
  font-size: 11px;
  line-height: 16px;
}
.ProfilePicture .Lettermark {
  width: inherit;
  height: inherit;
  font-size: inherit;
  line-height: inherit;
}

.ProfileBubbles {
  display: flex;
  align-items: center;
}
.ProfileBubbles > * {
  outline: 0.125rem solid var(--bg-surface-primary);
}
.ProfileBubbles > :not(:first-child) {
  margin-left: -0.125rem;
}

.ProfileBubbles__more {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 0.625rem;
  font-weight: 600;
  color: #fff;
  letter-spacing: -0.05em;
  user-select: none;
  background: var(--accent-primary);
  border-radius: 50%;
}
