.Spinner {
  --spinner-speed: 1s;
  --spinner-color: var(--accent-primary);
  display: inline-block;
  flex-shrink: 0;
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
}
.Spinner.Spinner--textColored {
  --spinner-color: currentColor;
}

.Spinner__layer {
  transform-origin: center;
}
.Spinner__layer > circle {
  display: block;
  fill: transparent;
  stroke: var(--spinner-color);
  stroke-linecap: round;
  stroke-width: 8px;
  transform-origin: center;
}
.Spinner__layer:nth-child(1) {
  opacity: 0.333;
}
.Spinner__layer:nth-child(2) {
  animation: spin var(--spinner-speed, 1s) infinite linear;
}
.Spinner__layer:nth-child(2) > circle {
  animation: Spinner__writhe calc(var(--spinner-speed, 1s) * 1.5) infinite ease both;
}

@keyframes Spinner__writhe {
  0%, 100% {
    stroke-dasharray: 70;
    stroke-dashoffset: -60;
  }
  50% {
    stroke-dasharray: 70;
    stroke-dashoffset: -30;
  }
}
.SpinnerOverlay {
  position: absolute;
  inset: 0;
  z-index: var(--z-content-overlay);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: min(100%, 6rem);
  text-align: center;
  transition: opacity 0.2s ease;
}
.SpinnerOverlay[aria-hidden=true] {
  pointer-events: none;
  opacity: 0;
}
.SpinnerOverlay::before {
  position: absolute;
  inset: 0;
  content: "";
  background: var(--bg-surface-primary);
  opacity: 0.5;
}
.SpinnerOverlay .Spinner {
  position: relative;
}
.SpinnerOverlay.SpinnerOverlay--scene-level::before {
  background: var(--bg-primary);
}
