.saved-insights .new-insight-dropdown-btn {
  height: 40px;
  padding: 8px 12px 8px 16px;
  color: white;
  cursor: pointer;
  background-color: var(--accent-primary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
}
.saved-insights .saved-insight-empty-state {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.new-insight-overlay {
  width: 21rem;
}

.insight-type-icon-wrapper {
  display: flex;
  align-items: center;
}
.insight-type-icon-wrapper .icon-container {
  position: relative;
  width: 22px;
  height: 22px;
  margin-right: 8px;
}
.insight-type-icon-wrapper .icon-container .icon-container-inner {
  margin-left: -2px;
  font-size: 22px;
}

.saved-insights-grid {
  position: relative;
  display: grid;
  grid-gap: 1rem;
}
@media screen and (min-width: 1200px) {
  .saved-insights-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 1600px) {
  .saved-insights-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
