.PropertyGroupFilters .property-group {
  padding: 0.5rem;
  background-color: var(--bg-primary);
  border-width: 1px;
  border-radius: var(--radius);
}
.PropertyGroupFilters .property-group-and-or-separator {
  position: relative;
  padding: 0.5rem 0;
  font-size: 12px;
  font-weight: 600;
  color: var(--primary-alt);
}
.PropertyGroupFilters .property-group-and-or-separator::before {
  position: absolute;
  top: 0;
  left: 17px;
  width: 2px;
  height: 100%;
  content: " ";
  background-color: var(--border-primary);
}
.PropertyGroupFilters .property-group-and-or-separator > span {
  position: relative;
  z-index: 1;
  display: inline-block;
  width: 35px;
  padding: 0.25rem;
  text-align: center;
  background-color: var(--bg-surface-primary);
}
