.ColumnConfiguratorModal .Columns {
  display: flex;
  column-gap: 1rem;
  width: 700px;
  padding: 0.5rem;
  background-color: var(--bg-primary);
  border-radius: var(--radius);
}
@media (max-width: 960px) {
  .ColumnConfiguratorModal .Columns {
    display: block;
    width: auto;
  }
}
.ColumnConfiguratorModal .HalfColumn {
  width: 50%;
}
@media (max-width: 960px) {
  .ColumnConfiguratorModal .HalfColumn {
    width: 100%;
  }
}
.ColumnConfiguratorModal .SelectedColumn {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 0.5rem;
  margin: calc(var(--radius) / 2) 0;
  overflow: hidden;
  background-color: var(--accent-primary-highlight);
  border-radius: var(--radius);
}
.ColumnConfiguratorModal .selected-column-col {
  display: flex;
  flex-direction: column;
}
.ColumnConfiguratorModal .drag-handle {
  padding-right: 0.25rem;
  font-size: 1.2em;
  color: var(--text-3000);
  cursor: move;
}
.ColumnConfiguratorModal .drag-handle svg {
  transform: rotate(90deg);
}
