.LemonSkeleton {
  background: linear-gradient(90deg, var(--color-skeleton-light) 25%, var(--color-skeleton-dark) 45%, var(--color-skeleton-light) 65%);
  background-size: 400% 100%;
  animation: LemonSkeleton__shimmer 2s ease infinite;
}
.LemonSkeleton.LemonSkeleton--static {
  animation: none;
}
.LemonSkeleton > span {
  display: block;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
}
@media (prefers-reduced-motion) {
  .LemonSkeleton {
    animation: none;
  }
}

@keyframes LemonSkeleton__shimmer {
  0% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
