.TaxonomicPropertyFilter {
  width: 100%;
  overflow: hidden;
}
.TaxonomicPropertyFilter--in-dropdown {
  width: 900px;
  min-width: 300px;
  max-width: 100%;
  background: var(--bg-surface-primary);
}
.TaxonomicPropertyFilter--in-dropdown .TaxonomicPropertyFilter__row-items > :first-child {
  width: 10rem;
}

.TaxonomicPropertyFilter__row {
  display: flex;
  gap: 0.5rem;
  overflow: hidden;
}

.TaxonomicPropertyFilter__row-operator {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: flex-end;
  width: 4.5rem;
  height: 40px;
}
.TaxonomicPropertyFilter__row--or-filtering .TaxonomicPropertyFilter__row-operator {
  width: 2rem;
}

.TaxonomicPropertyFilter__row-arrow {
  position: relative;
  font-size: 18px;
  font-weight: bold;
  color: var(--border-bold-3000);
  user-select: none;
}

.TaxonomicPropertyFilter__row-items {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
  overflow: hidden;
}
.TaxonomicPropertyFilter__row-items > * {
  max-width: 100%;
  overflow: hidden;
}
.TaxonomicPropertyFilter__row--showing-operators .TaxonomicPropertyFilter__row-items > :first-child {
  flex-grow: 1;
  width: 30%;
  min-width: 10rem;
}
