.ActivityLog.notifications-menu {
  max-width: 25rem;
}
.ActivityLog .ActivityDescription {
  padding: 1rem;
  background: var(--bg-surface-primary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
}
.ActivityLog .ActivityDescription .taxonomic-breakdown-filter.tag-pill {
  padding: 0;
  font-size: 0.8125rem;
  vertical-align: unset;
  background: none;
  border: none;
  border-radius: 0;
}
.ActivityLog .ActivityDescription section:not(:last-child) {
  margin-bottom: 0.5rem;
}
.ActivityLog .page-caption {
  max-width: 50rem;
}

.ActivityLogRow {
  display: flex;
  gap: 0.5rem;
  max-width: 50rem;
  padding: 0.5rem;
  line-height: 24px;
  overflow-wrap: anywhere;
}
.ActivityLogRow--unread {
  background-color: var(--accent-primary-highlight);
  border-radius: var(--radius);
}
.ActivityLogRow .ProfilePicture {
  flex-grow: 0;
}
.ActivityLogRow .ActivityLogRow__details {
  flex-grow: 1;
}
.ActivityLogRow .ActivityLogRow__details .highlighted-activity {
  display: inline;
  background-color: var(--mark);
}
.ActivityLogRow .ActivityLogRow__details .highlighted-activity > .PersonDisplay {
  min-width: 5.5rem;
}
.ActivityLogRow .ActivityLogRow__description ul.bullet-list {
  padding-left: 20px;
  list-style-type: disc;
}
.ActivityLogRow .ActivityLogRow__description ul.bullet-list li {
  display: list-item;
}
.ActivityLogRow .ActivityLogRow__description__extended {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}
