.LemonTag {
  display: inline-flex;
  gap: 0.125rem;
  align-items: center;
  padding: 0.125rem 0.25rem;
  font-size: 0.75rem;
  font-weight: var(--font-medium);
  line-height: 1rem;
  color: var(--text-3000);
  white-space: nowrap;
  background: var(--border-primary);
  border-radius: var(--radius-sm);
}
.LemonTag.LemonTag--normal {
  font-weight: normal;
}
.LemonTag:not(.LemonTag--breakdown) {
  padding: 0.075rem 0.25rem;
  font-size: 0.6875rem;
  background: none;
  border-style: solid;
  border-width: 1px;
  border-radius: var(--radius-sm);
}
.LemonTag.LemonTag--primary {
  color: var(--accent-primary);
  background: none;
  border-color: var(--accent-primary);
}
.LemonTag.LemonTag--option {
  color: var(--accent-primary);
  background-color: var(--bg-surface-primary);
}
.LemonTag.LemonTag--highlight {
  color: var(--accent-primary);
  background: none;
  border-color: var(--accent-primary);
}
.LemonTag.LemonTag--warning {
  color: var(--warning);
  background-color: none;
  border-color: var(--warning);
}
.LemonTag.LemonTag--danger {
  color: var(--danger);
  background: none;
  border-color: var(--danger);
}
.LemonTag.LemonTag--success {
  color: var(--success);
  background: none;
  border-color: var(--success);
}
.LemonTag.LemonTag--completion {
  color: var(--purple);
  background: none;
  border-color: var(--purple);
}
.LemonTag.LemonTag--caution {
  color: var(--danger-lighter);
  background: none;
  border-color: var(--danger-lighter);
}
.LemonTag.LemonTag--muted {
  color: var(--text-secondary);
  background-color: var(--bg-surface-primary);
}
.LemonTag.LemonTag--none {
  background: none;
}
.LemonTag.LemonTag--breakdown {
  padding: 8px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: var(--primary-alt);
  vertical-align: bottom;
  background-color: var(--accent-primary-highlight);
  border-radius: 40px;
}
.LemonTag.LemonTag--size-small {
  padding: 0 0.1875rem;
  font-size: 0.625rem;
  line-height: 0.875rem;
}
.LemonTag .LemonTag__icon {
  display: flex;
  margin-right: 0.125rem;
  font-size: 0.875rem;
}
.LemonTag .LemonTag__right-button {
  min-height: 1.5rem !important;
  padding: 0.125rem !important;
  margin-left: 0.25rem;
}
