.correlation-table-wrapper table {
  margin: 0 auto;
  border-spacing: 0;
  border-collapse: separate;
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
}
.correlation-table-wrapper table td {
  padding: 0.5rem 1rem;
  text-align: center;
  border-top: 1px solid var(--border-primary);
  border-left: 1px solid var(--border-primary);
}
.correlation-table-wrapper table td .percentage {
  padding-bottom: 0.25rem;
  font-weight: bold;
}
.correlation-table-wrapper table td:first-child {
  border-left: none;
}
.correlation-table-wrapper table .table-title {
  color: var(--text-secondary);
}
.correlation-table-wrapper table .table-title td {
  border-top: none;
}
.correlation-table-wrapper table thead,
.correlation-table-wrapper table .horizontal-header {
  font-weight: bold;
  color: var(--text-secondary);
  text-transform: uppercase;
  background-color: var(--bg-primary);
}
.correlation-table-wrapper table tbody td:not(.horizontal-header) {
  text-align: right;
}
