.LemonBanner {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-height: 3rem;
  padding: 0.5rem;
  font-weight: 500;
  color: var(--text-primary);
  text-align: left;
  border: solid 1px var(--border-primary);
  border-radius: var(--radius);
}
.LemonBanner.LemonBanner--square {
  border-radius: 0;
}
.LemonBanner.LemonBanner--info {
  background-color: var(--bg-fill-primary);
  border-color: var(--border-primary);
}
.LemonBanner.LemonBanner--warning {
  color: var(--text-warning-on-bg-fill);
  background-color: var(--bg-fill-warning-secondary);
  border-color: var(--border-warning);
}
.LemonBanner.LemonBanner--error {
  color: var(--text-error-on-bg-fill);
  background-color: var(--bg-fill-error-secondary);
  border-color: var(--border-error);
}
.LemonBanner.LemonBanner--success {
  color: var(--text-success-on-bg-fill);
  background-color: var(--bg-fill-success-secondary);
  border-color: var(--border-success);
}
.LemonBanner .LemonBanner__icon {
  flex-shrink: 0;
  font-size: 1.5rem;
  line-height: 0;
}
.LemonBanner p {
  margin-bottom: 0.25em;
}
.LemonBanner p:last-child {
  margin-bottom: 0;
}
