.ActionsPie {
  display: flex;
  flex: 1;
  flex-direction: row;
  gap: 0.5rem;
  width: 100%;
  height: 100%;
}
.ActionsPie .ActionsPie__component {
  flex: 1;
  min-width: 33%;
  padding: 1rem;
}
.ActionsPie .ActionsPie__component .ActionsPie__chart {
  position: relative;
  width: 100%;
  height: calc(100% - 4.5rem);
  transition: height 0.5s;
}
.ActionsPie .ActionsPie__component h3 {
  letter-spacing: -0.025em;
}
