.LemonInput {
  --lemon-input-height: calc(2.125rem + 3px);
  display: flex;
  gap: 0.375rem;
  align-items: center;
  justify-content: left;
  height: var(--lemon-input-height);
  min-height: var(--lemon-input-height);
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: var(--text-3000);
  text-align: left;
  cursor: text;
  background: none;
  background-color: var(--bg-fill-input);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
}
.LemonInput[aria-disabled=true] {
  cursor: not-allowed;
}
.LemonInput:hover:not([aria-disabled=true]), .LemonInput.LemonInput--focused:not([aria-disabled=true]) {
  border-color: var(--border-secondary);
}
.LemonInput.LemonInput--transparent-background {
  background-color: inherit;
}
.Field--error .LemonInput:not(.ignore-error-border), .LemonInput.LemonInput--status-danger {
  border-color: var(--danger) !important;
}
.LemonInput .LemonInput__input {
  flex: 1;
  align-self: stretch;
  width: 100%;
  padding: 0;
  cursor: inherit;
  background: none;
  border: none;
  outline: none;
}
.LemonInput .LemonInput__input:disabled {
  opacity: var(--opacity-disabled);
}
.LemonInput .LemonIcon {
  flex-shrink: 0;
  width: 1em;
  height: 1em;
  font-size: 1.25rem;
  color: var(--text-secondary);
  transition: color 200ms ease;
}
.LemonInput.LemonInput--xsmall {
  --lemon-input-height: 1.5rem;
  padding: 0.125rem 0.25rem;
}
.LemonInput.LemonInput--xsmall .LemonIcon {
  font-size: 1rem;
}
.LemonInput.LemonInput--small {
  --lemon-input-height: 2rem;
  padding: 0.125rem 0.25rem;
}
.LemonInput.LemonInput--small .LemonIcon {
  font-size: 1.25rem;
}
.LemonInput.LemonInput--large {
  --lemon-input-height: 3rem;
}
.LemonInput.LemonInput--large .LemonIcon {
  font-size: 1.5rem;
}
.LemonInput.LemonInput--has-content > .LemonIcon {
  color: var(--accent-primary);
}
.LemonInput.LemonInput--type-search {
  max-width: 240px;
}
.LemonInput.LemonInput--type-number .LemonInput__input {
  font-variant-numeric: tabular-nums;
  text-overflow: clip;
}
.LemonInput.LemonInput--full-width {
  width: 100%;
  max-width: 100%;
}
