.BillingPlan {
  flex-grow: 1;
  max-width: 500px;
}
.BillingPlan .BillingPlan__description ol,
.BillingPlan .BillingPlan__description ul {
  padding-left: 0;
  margin-top: 1rem;
  text-align: center;
  list-style: none;
}
.BillingPlan .BillingPlan__description ol li,
.BillingPlan .BillingPlan__description ul li {
  margin-bottom: 1rem;
  line-height: 1.2rem;
}
.BillingPlan .BillingPlan__description ol .disclaimer,
.BillingPlan .BillingPlan__description ul .disclaimer {
  padding-left: 1px;
  font-size: 10px;
  font-weight: bold;
  vertical-align: top;
}
.BillingPlan .BillingPlan__description .disclaimer-details {
  margin-top: 1rem;
  font-size: 0.75rem;
  color: var(--text-secondary);
}
