.InsightCard {
  position: relative;
  z-index: 3;
  display: flex;
  flex-direction: column;
  min-width: 0;
  max-width: 100%;
  height: 30rem;
  background: var(--bg-surface-primary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
  transition: border 200ms ease;
}
.InsightCard--highlighted {
  border-color: var(--accent-primary);
  outline: 1px solid var(--accent-primary);
}
.InsightCard .ErrorBoundary {
  width: 100%;
  height: 100%;
  margin: auto;
}

.InsightCard__viz {
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 100%;
  overflow: auto;
  border-radius: 0 0 var(--radius) var(--radius);
}
.InsightCard__viz .LineGraph,
.InsightCard__viz .AnnotationsOverlay {
  padding: 0.5rem;
}
.InsightCard__viz .LemonTable {
  background: none;
  border: none;
  border-radius: 0;
}
.WebAnalyticsDashboard .InsightVizDisplay .InsightCard__viz {
  min-height: var(--insight-viz-min-height);
}

.InsightDetails,
.CardMeta .InsightDetails {
  font-size: 0.8125rem;
  line-height: 1.5rem;
}
.InsightDetails h5,
.CardMeta .InsightDetails h5 {
  margin-bottom: 0.125rem;
  line-height: 1.5rem;
}
.InsightDetails section:not(:last-child),
.CardMeta .InsightDetails section:not(:last-child) {
  margin-bottom: 0.5rem;
}
.InsightDetails .LemonRow,
.CardMeta .InsightDetails .LemonRow {
  min-height: 2rem;
  font-size: inherit;
}

.InsightDetails__query {
  padding: 0.375rem 0.5rem;
  margin-top: 0.25rem;
  border-width: 1px;
  border-radius: var(--radius);
}
.InsightDetails__query .LemonRow {
  padding-right: 0;
  padding-left: 0;
}

.InsightDetails__formula code {
  margin-left: 0.375rem;
  font-weight: 600;
}

.InsightDetails__series {
  margin: -0.125rem 0;
}
.InsightDetails__series:not(:first-child) {
  margin-top: 0.5rem;
}
.InsightDetails__series .LemonDivider {
  width: calc(100% - 1.5rem);
  margin-left: 1.5rem;
}

.InsightDetails__footer {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem 2rem;
}
.InsightDetails__footer .profile-package {
  vertical-align: middle;
}
.InsightDetails__footer .taxonomic-breakdown-filter.tag-pill {
  padding: 0;
  font-size: 0.8125rem;
  vertical-align: unset;
  background: none;
  border: none;
  border-radius: 0;
}

.SeriesDisplay {
  line-height: 1.5rem;
}

.SeriesDisplay__raw-name {
  display: inline-flex;
  align-items: center;
  padding: 0.125rem 0.25rem;
  margin: 0 0.25rem;
  font-size: 0.6875rem;
  font-weight: 600;
  line-height: 1rem;
  color: var(--text-secondary);
  vertical-align: middle;
  background: var(--accent-primary-highlight);
  border-radius: var(--radius);
}
.SeriesDisplay__raw-name.SeriesDisplay__raw-name--action, .SeriesDisplay__raw-name.SeriesDisplay__raw-name--event {
  padding: 0.25rem;
}
.SeriesDisplay__raw-name.SeriesDisplay__raw-name--action::before, .SeriesDisplay__raw-name.SeriesDisplay__raw-name--event::before {
  display: inline-block;
  flex-shrink: 0;
  width: 1rem;
  margin-right: 0.25rem;
  font-size: 0.625rem;
  font-weight: 700;
  line-height: 1rem;
  color: var(--bg-surface-primary);
  text-align: center;
  background: var(--accent-primary);
  border-radius: var(--radius);
}
.SeriesDisplay__raw-name.SeriesDisplay__raw-name--action::before {
  content: "A";
}
.SeriesDisplay__raw-name.SeriesDisplay__raw-name--event::before {
  content: "E";
}

.SeriesDisplay__condition {
  display: flex;
}

.SeriesDisplay__arrow {
  flex-shrink: 0;
  margin-right: 0.25rem;
  font-size: 1.25rem;
  color: var(--text-secondary);
}
