.CodeSnippet {
  position: relative;
  font-size: 0.875rem;
}
.CodeSnippet.CodeSnippet--compact {
  font-size: 0.8125rem;
}
.CodeSnippet.CodeSnippet--compact .CodeSnippet__actions {
  top: 0.375rem;
  right: 0.375rem;
}
.CodeSnippet.CodeSnippet--compact pre {
  padding-top: 0.375rem;
}
.CodeSnippet .CodeSnippet__actions {
  position: absolute;
  top: 0.75rem;
  right: 0.5rem;
  display: flex;
  gap: 0.5rem;
  background: var(--bg-surface-primary) !important;
}
.CodeSnippet pre {
  padding: 0.75rem 1rem;
  background: var(--bg-surface-primary) !important;
  border: solid 1px var(--border-primary) !important;
  border-radius: var(--radius) !important;
}
