.LemonDivider {
  flex-shrink: 0;
  width: 100%;
  height: 1px;
  background: var(--border-primary);
}
.LemonDivider--dashed {
  background: repeating-linear-gradient(to right, var(--border-primary), var(--border-primary) 4px, transparent 4px, transparent 8px);
}
.LemonDivider--thick {
  height: 3px;
}
.LemonDivider--vertical {
  align-self: stretch;
  width: 1px;
  height: auto;
}
.LemonDivider--vertical.LemonDivider--thick {
  width: 3px;
}
.LemonDivider--vertical.LemonDivider--dashed {
  background: repeating-linear-gradient(to bottom, var(--border-primary), var(--border-primary) 4px, transparent 4px, transparent 8px);
}
.LemonDivider--with-label {
  display: flex;
  height: unset;
  background: none;
}
.LemonDivider--with-label::before, .LemonDivider--with-label::after {
  position: relative;
  top: 50%;
  display: block;
  width: 50%;
  content: "";
  border-top: 1px solid var(--border-primary);
  transform: translateY(50%);
}
