.ErrorBoundary {
  min-width: 0;
  height: fit-content;
  padding: 0.75rem 1rem 1rem;
  margin: 1.5rem;
  background: var(--danger-highlight);
  border-radius: var(--radius);
}
.ErrorBoundary h2 {
  margin-bottom: 0.75rem;
  font-weight: 600;
  color: var(--danger);
}
.ErrorBoundary pre {
  padding: 0.75rem 1rem;
  margin-bottom: 0.75rem;
  background: var(--border-primary);
  border-radius: var(--radius);
}
.ErrorBoundary .help-button {
  margin-top: 0.75rem;
}
