.PlayerFrame {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: var(--bg-3000-dark);
}
.PlayerFrame .PlayerFrame__content--masking-window .replayer-wrapper::before {
  position: absolute;
  inset: 0; /* Cover the entire wrapper */
  z-index: 10; /* Ensure it's above the iframe */
  pointer-events: auto; /* Block interactions */
  content: "";
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: repeat;
  background-size: 16px 16px;
}
.PlayerFrame .PlayerFrame__content {
  position: absolute;
}
.PlayerFrame .PlayerFrame__content iframe {
  background-color: white;
  border: 0;
}
.PlayerFrame .PlayerFrame__content .replayer-wrapper {
  position: relative;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse {
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  border-color: transparent; /* otherwise we transition from black when .touch-device class is added */
  transition: left 0.05s linear, top 0.05s linear;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse::after {
  display: inline-block;
  width: 20px;
  height: 20px;
  content: "";
  background: rgb(73, 80, 246);
  border-radius: 100%;
  opacity: 0.3;
  transform: translate(-50%, -50%);
}
.PlayerFrame .PlayerFrame__content .replayer-mouse.active::after {
  animation: PlayerFrame__click 0.2s ease-in-out 1;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse.touch-device {
  width: 70px;
  height: 70px;
  margin-top: -37px;
  margin-left: -37px;
  background-image: none; /* there's no passive cursor on touch-only screens */
  border-color: rgba(73, 80, 246, 0);
  border-style: solid;
  border-width: 4px;
  border-radius: 100%;
  transition: left 0s linear, top 0s linear, border-color 0.2s ease-in-out;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse.touch-device.touch-active {
  border-color: rgb(73, 80, 246);
  transition: left 0.25s linear, top 0.25s linear, border-color 0.2s ease-in-out;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse.touch-device::after {
  opacity: 0; /* there's no passive cursor on touch-only screens */
}
.PlayerFrame .PlayerFrame__content .replayer-mouse.touch-device.active::after {
  animation: PlayerFrame__touch-click 0.2s ease-in-out 1;
}
.PlayerFrame .PlayerFrame__content .replayer-mouse-tail {
  position: absolute;
  pointer-events: none;
}
@keyframes PlayerFrame__click {
  0% {
    width: 20px;
    height: 20px;
    opacity: 0.3;
  }
  50% {
    width: 10px;
    height: 10px;
    opacity: 0.5;
  }
}
@keyframes PlayerFrame__touch-click {
  0% {
    width: 20px;
    height: 20px;
    opacity: 0;
  }
  50% {
    width: 10px;
    height: 10px;
    opacity: 0.5;
  }
}
