.LemonTabs {
  --lemon-tabs-gap: 2rem;
  --lemon-tabs-margin-bottom: 1rem;
  --lemon-tabs-content-padding: 0.75rem 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-self: stretch;
}
.LemonTabs--small {
  --lemon-tabs-gap: 1rem;
  --lemon-tabs-margin-bottom: 0.5rem;
  --lemon-tabs-content-padding: 0.375rem 0;
}
.Navigation3000__scene > .LemonTabs:first-child, .Navigation3000__scene > :first-child > .LemonTabs:first-child {
  margin-top: -0.75rem;
}

.LemonTabs__bar {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  gap: var(--lemon-tabs-gap);
  align-items: stretch;
  margin-bottom: var(--lemon-tabs-margin-bottom);
  overflow-x: auto;
  list-style: none;
}
.LemonTabs__bar::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  content: "";
  background: var(--border-primary);
}
.LemonTabs__bar::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: var(--lemon-tabs-slider-width);
  height: 0.125rem;
  content: "";
  background: var(--accent-primary);
  transform: translateX(var(--lemon-tabs-slider-offset));
}
.LemonTabs--transitioning .LemonTabs__bar::after {
  transition: width 150ms ease, transform 150ms ease;
}
.LemonTabs--transitioning .LemonTabs__bar .LemonTabs__tab {
  transition: color 150ms ease;
}
.LemonTabs__bar .LemonTabs__tab:hover {
  color: var(--accent-primary);
}
.LemonTabs__bar .LemonTabs__tab:active {
  color: var(--accent-primary);
}
.LemonTabs__bar .LemonTabs__tab.LemonTabs__tab--active {
  color: var(--accent-primary);
  text-shadow: 0 0 0.25px currentColor;
}
.LemonTabs__bar .LemonTabs__tab a {
  color: inherit;
  text-decoration: none;
  transition: none;
}
.LemonTabs__bar .LemonTabs__tab .LemonTabs__tab-content {
  display: flex;
  align-items: center;
  padding: var(--lemon-tabs-content-padding);
  white-space: nowrap;
  cursor: pointer;
}
