.LemonCollapse {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  overflow: hidden;
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
}
.LemonCollapse--embedded {
  border: none;
  border-radius: 0;
}

.LemonCollapsePanel {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.LemonCollapsePanel:not(:last-child) {
  border-bottom-width: 1px;
}

.LemonCollapsePanel__header {
  background: var(--bg-surface-primary);
  border-radius: 0 !important;
}
.LemonCollapsePanel__header.LemonButton:active {
  transform: inherit;
}
.LemonCollapsePanel__header--disabled:hover {
  cursor: default;
  background-color: var(--bg-surface-primary) !important;
}

.LemonCollapsePanel__body {
  box-sizing: content-box;
  height: 0;
  overflow: hidden;
  border-top-width: 1px;
  transition: height 200ms ease;
}
.storybook-test-runner .LemonCollapsePanel__body {
  height: fit-content !important;
}

.LemonCollapsePanel__content {
  padding: 1rem;
}
