.RetentionTable {
  width: 100%;
  font-weight: 500;
}
.RetentionTable th {
  padding: 0 0.5rem;
  font-weight: 500;
  text-align: left;
  white-space: nowrap;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 1px solid var(--border-primary);
}
.RetentionTable th:first-of-type {
  border-left: none;
}
.RetentionTable tr {
  border-left: 0.25rem solid transparent;
}
.RetentionTable tr:not(:nth-of-type(-n + 2)) {
  cursor: pointer;
}
.RetentionTable tr:not(:nth-of-type(-n + 2)):hover {
  border-left-color: var(--retention-table-color);
}
.RetentionTable .RetentionTable__TextTab {
  padding: 0 1rem 0 0.5rem;
  white-space: nowrap;
}
.RetentionTable .RetentionTable__Tab {
  min-height: 2rem;
  padding: 0.5rem;
  margin: 2px;
  text-align: right;
  background-color: var(--retention-table-color);
  border-radius: var(--radius);
}
.RetentionTable .RetentionTable__Tab.RetentionTable__Tab--clickable {
  transition: transform 200ms ease-out;
}
.RetentionTable .RetentionTable__Tab.RetentionTable__Tab--clickable:hover {
  transform: scale(1.05);
}
.RetentionTable .RetentionTable__Tab.RetentionTable__Tab--period {
  color: var(--text-secondary);
  background-color: var(--bg-surface-primary);
  border: 1px solid var(--border-primary);
  border-style: dashed;
}
.RetentionTable.RetentionTable--non-interactive tr:hover {
  border-left-color: transparent;
}
.RetentionTable.RetentionTable--non-interactive .RetentionTable__Tab {
  cursor: initial;
}
.RetentionTable.RetentionTable--non-interactive .RetentionTable__Tab:hover {
  transform: none;
}
.RetentionTable.RetentionTable--small-layout {
  font-size: 0.75rem;
  line-height: 1rem;
}
.RetentionTable.RetentionTable--small-layout th {
  padding-right: 0.25rem;
  padding-left: 0.25rem;
}
.RetentionTable.RetentionTable--small-layout .RetentionTable__TextTab {
  padding-right: 0.25rem;
  padding-left: 0.25rem;
}
.RetentionTable.RetentionTable--small-layout .RetentionTable__Tab {
  padding: 0.5rem 0.25rem;
  margin: 0;
}
