*,
::after,
::before,
::backdrop,
::file-selector-button {
  border-color: var(--border-primary, currentColor);
}

.rendering-pixelated {
  image-rendering: pixelated;
}

input[type=text] {
  font-weight: 400;
}

input[type=radio] {
  accent-color: var(--accent-primary);
}

input[type=search]::-webkit-search-decoration, input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-results-button, input[type=search]::-webkit-search-results-decoration {
  display: none;
}

body {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5715;
  color: var(--text-3000);
  color-scheme: light dark;
  touch-action: manipulation;
  background: var(--bg-primary);
}
body[theme=light] {
  color-scheme: light;
}
body[theme=dark] {
  color-scheme: dark;
}
body.is-resizing {
  cursor: grabbing !important;
}
body.is-resizing *:not(.Resizer) {
  pointer-events: none;
}
body * > ::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
body * > ::-webkit-scrollbar-track {
  background: var(--bg-surface-primary);
}
body * > ::-webkit-scrollbar-thumb {
  background: var(--trace-3000);
  border-radius: var(--radius);
}
body * > ::-webkit-scrollbar-thumb:hover {
  background: var(--muted-3000);
}
body b,
body strong {
  font-weight: 600;
}
body h1,
body h2,
body h3:where(:not(.thank-you-message)),
body h4,
body h5 {
  margin-top: 0;
  margin-bottom: 0.5em;
  font-family: var(--font-title);
  font-weight: 500;
}
body h1,
body h2,
body h3,
body h4 {
  color: var(--text-3000);
}
body h1 {
  font-size: 1.75rem;
}
body h2 {
  font-size: 1.3125rem;
}
body h3 {
  font-size: 1rem;
}
body h5 {
  margin-bottom: 0.25rem;
  font-size: 0.6875rem;
  font-weight: 600;
  line-height: 1.5rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.075em;
}
body pre {
  margin-top: 0;
  margin-bottom: 1em;
  overflow: auto;
}
body dialog {
  background-color: var(--bg-primary);
}
body .LemonButton .text-link,
body .Link .text-link {
  color: var(--text-3000) !important;
}
body .LemonButton:hover .text-link,
body .Link:hover .text-link {
  color: var(--accent-primary) !important;
}

.page-title-row {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem 1rem;
  align-items: center;
  min-height: 2.5rem;
  margin: 1rem 0 0.25rem;
}
@media screen and (min-width: 768px) {
  .page-title-row {
    flex-wrap: nowrap;
  }
}

.page-title {
  min-width: 0;
  margin: 0;
  font-size: 28px;
  font-weight: 600;
  line-height: 34px;
}

.page-caption {
  max-width: 48rem;
  margin-bottom: 1rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.page-caption.tabbed {
  margin-bottom: 0.5rem;
}

.secondary {
  font-size: 0.9em;
  font-weight: var(--font-medium);
  color: var(--primary-alt);
  text-transform: uppercase;
  letter-spacing: 1px;
  background: none;
  border: 0;
}

.card-secondary {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

mark {
  background-color: var(--mark) !important;
  border-radius: var(--radius);
}

hr {
  margin: 1em 0;
  border-top: 1px solid var(--border);
}

dfn {
  font-style: normal;
  text-decoration: underline;
  text-decoration-style: dotted;
}

input::-ms-clear {
  display: none;
}

.Toastify__toast {
  align-items: center;
  padding: 0.75rem;
  margin-bottom: 0.5rem !important;
  font-family: var(--font-sans);
  font-size: 1rem;
  cursor: unset;
  border: 1px solid var(--secondary-3000-button-border);
  border-radius: var(--radius);
  box-shadow: var(--shadow-elevation-3000);
  opacity: 1 !important;
}

.Toastify__toast-container {
  padding: 0;
}

.Toastify__toast-body {
  padding: 0;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.25rem;
  color: var(--text-3000);
}
.Toastify__toast-body b,
.Toastify__toast-body strong {
  font-weight: 700;
}
.Toastify__toast-body button {
  margin: 0 0.75rem;
}

.Toastify__toast-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.75rem;
  font-size: 1.5rem;
  color: var(--text-secondary);
}
.Toastify__toast--success .Toastify__toast-icon {
  color: var(--toastify-color-success);
}
.Toastify__toast--warning .Toastify__toast-icon {
  color: var(--toastify-color-warning);
}
.Toastify__toast--error .Toastify__toast-icon {
  color: var(--toastify-color-error);
}

.Toastify__progress-bar {
  height: var(--radius);
}

.error-on-blur.errored:not(:focus) {
  border-color: var(--danger) !important;
}

.info-indicator {
  margin-left: 4px;
  color: var(--primary-alt) !important;
  cursor: pointer;
}
.info-indicator.left {
  margin-right: 4px;
  margin-left: 0;
}

.graph-series-glyph {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  font-size: 12px;
  font-weight: bold;
  line-height: 1rem;
  pointer-events: none;
  border: 2px solid var(--text-3000);
  border-radius: 50%;
}
.graph-series-glyph.funnel-step-glyph {
  width: 23px;
  height: 23px;
  font-size: unset;
  color: var(--primary-alt);
  background-color: var(--bg-surface-primary);
  border-color: var(--border);
}

#bottom-notice {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: var(--z-bottom-notice);
  display: flex !important;
  flex-direction: row;
  width: 100%;
  font-size: 0.75rem;
  line-height: 1.5rem;
  color: #fff;
  background: #000;
}
#bottom-notice code {
  font-size: inherit;
  color: inherit;
}
#bottom-notice.warning div {
  height: auto;
  background: var(--danger);
}
#bottom-notice.tricolor {
  cursor: pointer;
}
#bottom-notice.tricolor div:nth-child(1) {
  background: var(--brand-blue);
}
#bottom-notice.tricolor div:nth-child(2) {
  background: var(--brand-red);
}
#bottom-notice.tricolor div:nth-child(3) {
  background: var(--brand-yellow);
}
#bottom-notice.tricolor span {
  cursor: text;
}
#bottom-notice div {
  flex-basis: 0;
  flex-grow: 1;
  height: 1.5rem;
  text-align: center;
}
#bottom-notice span {
  display: none;
}
#bottom-notice button {
  width: 1.5rem;
  height: 1.5rem;
  padding: 0;
  font-size: 1rem;
  font-weight: bold;
  color: inherit;
  cursor: pointer;
  background: transparent;
  border: none;
}
@media screen and (min-width: 750px) {
  #bottom-notice {
    font-size: 1rem;
    line-height: 2rem;
  }
  #bottom-notice div {
    height: 2rem;
  }
  #bottom-notice span {
    display: inline;
  }
  #bottom-notice button {
    width: 2rem;
    height: 2rem;
    font-size: 1.25rem;
  }
}

